function createTab(key) {
    const tab = createElement('li', 'section__tab');
    if (store.activeTab=== key) {
        tab.classList.add('section__tab_active')
    }
    setAttrs(tab, {
        role: 'tab',
        'aria-selected': store.activeTab === key ? 'true' : 'false',
        tabindex: '0',
        id: 'tab_' + key,
        'aria-controls': 'panel_' + tab,
    })
    tab.textContent = TABS_TITLES[key];
    return tab;
}
