function createPanelAndList(key) {
    const panel = createElement('div', 'section__panel', 'section__panel_hidden');
    setAttrs(panel, {
        role: 'tabpanel',
        'aria-hidden': key === store.activeTab ? 'false' : 'true',
        'aria-labelledby': 'tabs_' + key,
        id: 'panel_' + key
    })

    const panelList = createElement('ul', 'section__panel-list');
    panel.appendChild(panelList);
    if (key === store.activeTab) {
        panel.classList.remove('section__panel_hidden');
        panelList.append(...panelsContent[key]);
    }
    return [panel, panelList];
}

const panelsAndListNodes = {
    all: createPanelAndList('all'),
    kitchen: createPanelAndList('kitchen'),
    hall: createPanelAndList('hall'),
    lights: createPanelAndList('lights'),
    cameras: createPanelAndList('cameras'),
}

const arrow = createElement('div', 'section__arrow');
arrow.onclick = function () {
    const scroller = panelsAndListNodes[store.activeTab][0];
    scroller.scrollTo({
        left: scroller.scrollLeft + 400,
        behavior: 'smooth'
    });      
}

function hasRightScroll(key) {
    const panelList = panelsAndListNodes[key][1];
    return panelList.scrollWidth > panelWrapper.offsetWidth
}


panelWrapper.append(
    panelsAndListNodes.all[0],
    panelsAndListNodes.kitchen[0],
    panelsAndListNodes.hall[0],
    panelsAndListNodes.lights[0],
    panelsAndListNodes.cameras[0],  
);
if (hasRightScroll(store.activeTab)) {
    panelWrapper.append(arrow);
}

function setActiveTab(key) {
    if (key === store.activeTab) return;

    const prevTab = tabNodes[store.activeTab];
    prevTab.setAttribute('aria-selected', 'false');
    prevTab.removeAttribute('tab-index');
    prevTab.classList.remove('section__tab_active');

    const currTab = tabNodes[key];
    currTab.setAttribute('aria-selected', 'true');
    currTab.setAttribute('tab-index', '0');
    currTab.classList.add('section__tab_active');

    const [prevPanel, prevPanelList] = panelsAndListNodes[store.activeTab];
    prevPanel.setAttribute('class', 'section__panel section__panel_hidden')
    prevPanel.setAttribute('aria-hidden', 'true'); 
    prevPanelList.innerHTML = '';

    const [currPanel, currPanelList] = panelsAndListNodes[key];
    currPanel.setAttribute('class', 'section__panel')
    currPanel.setAttribute('aria-hidden', 'false');
    currPanelList.append(...panelsContent[key]);
    
    const hasrs = hasRightScroll(key);
    if (!arrow.parentNode && hasrs) {
        panelWrapper.appendChild(arrow);
    } else if (!hasrs) {
        arrow.remove();
    }

    store.activeTab = key;
}

select.oninput = (event) => {
    setActiveTab(event.target.value);
}

document.addEventListener('click', (event) => {
    for (let key in tabNodes) {
        if (event.target === tabNodes[key]) {
            setActiveTab(key);
            return;
        }
    }
})


